import React from "react";
import "./IITDAngelNetworkHeader.css";
import { BiMenuAltLeft } from "react-icons/bi";
// import { CgProfile } from "react-icons/cg";
import sinc from "./sinc.png";
import Avatar from "@material-ui/core/Avatar";
function Header() {
  return (
    <div className="iitdangelnetworkheader">
      <div className="iitdangelnetworkheaderDashboard">
        <div className="iitdangelnetworkheaderIcon">
          <BiMenuAltLeft size={30} />
        </div>
        <div className="iitdangelnetworkheaderDashboard_dashboard">
          Dashboard
        </div>
      </div>
      <div className="iitdangelnetworkheaderLogo">
        <div className="iitdangelnetworkheaderCellName">
          <img src={sinc} alt="logo" />
          <span> Student Incubation Cell</span>
        </div>
        <div className="iitdangelnetworkheaderWork">
          Mentors | Startups | Initiatives
        </div>
      </div>
      <div className="iitdangelnetworkheaderAvatar">
        <div className="iitdangelnetworkheaderPersonName">James Bond&nbsp;</div>
        <div className="iitdangelnetworkheaderPersonAvatar">
          <Avatar
            style={{ height: "30px", width: "30px" }}
            src="data:image/jpeg;base64,"
            alt="James Bond"
          />
        </div>
      </div>
    </div>
  );
}

export default Header;
