import React from "react";
import "./AngelNetworkNmHeader.css";
import { BiMenuAltLeft } from "react-icons/bi";
// import { CgProfile } from "react-icons/cg";
import sinc from "./sinc.png";
import Avatar from "@material-ui/core/Avatar";
function Header() {
  return (
    <div className="angelnetworknmheader">
      <div className="angelnetworknmheaderDashboard">
        <div className="angelnetworknmheaderIcon">
          <BiMenuAltLeft size={30} />
        </div>
        <div className="angelnetworknmheaderDashboard_dashboard">Dashboard</div>
      </div>
      <div className="angelnetworknmheaderLogo">
        <div className="angelnetworknmheaderCellName">
          <img src={sinc} alt="logo" />
          <span> Student Incubation Cell</span>
        </div>
        <div className="angelnetworknmheaderWork">
          Mentors | Startups | Initiatives
        </div>
      </div>
      <div className="angelnetworknmheaderAvatar">
        <div className="angelnetworknmheaderPersonName">James Bond&nbsp;</div>
        <div className="angelnetworknmheaderPersonAvatar">
          <Avatar
            style={{ height: "30px", width: "30px" }}
            src="data:image/jpeg;base64,"
            alt="James Bond"
          />
        </div>
      </div>
    </div>
  );
}

export default Header;
