import React from "react";
import "./Mentors_StartUpsYouFollowInfocard.css";
import { AiOutlineMail } from "react-icons/ai";
import Avatar from "@material-ui/core/Avatar";

function Infocard() {
  return (
    <div className="mentorsstartupsyoufollowInfocard">
      <div className="mentorsstartupsyoufollowInfocard_Avatar">
        <Avatar
          style={{ height: "120px", width: "120px" }}
          src="data:image/jpeg;base64,"
          alt="James Bond"
        />
      </div>
      <div className="mentorsstartupsyoufollowInfocard_Content">
        <div className="mentorsstartupsyoufollowInfocard_Head">
          <p>
            <h4 id="mentorsstartupsyoufollowInfocard_GreenIndie">
              Green Indie
            </h4>
            Senior consultant, BCG
          </p>
          <div className="mentorsstartupsyoufollowInfocard_MailIcon">
            <AiOutlineMail />
          </div>
        </div>
        <p>
          <select className="">
            <option value="">Following</option>
          </select>
        </p>
        <Avatar 
          src="data:image/jpeg;base64,"
          alt="Lorem Ipsum"
        />
        {/* <p>
                    Link: <span className="Infocard_Span">www.recools.com</span><br />
                    POC: <span className="Infocard_Span">Deeoak Nanda</span>
                </p> */}
      </div>
    </div>
  );
}

export default Infocard;
