import React from "react";
import "./AngelNetworkInvestorHeader.css";
import { BiMenuAltLeft } from "react-icons/bi";
// import { CgProfile } from "react-icons/cg";
import sinc from "./sinc.png";
import Avatar from "@material-ui/core/Avatar";
function Header() {
  return (
    <div className="angelnetworkinvestorheader">
      <div className="angelnetworkinvestorheaderDashboard">
        <div className="angelnetworkinvestorheaderIcon">
          <BiMenuAltLeft size={30} />
        </div>
        <div className="angelnetworkinvestorheaderDashboard_dashboard">
          Dashboard
        </div>
      </div>
      <div className="angelnetworkinvestorheaderLogo">
        <div className="angelnetworkinvestorheaderCellName">
          <img src={sinc} alt="logo" />
          <span> Student Incubation Cell</span>
        </div>
        <div className="angelnetworkinvestorheaderWork">
          Mentors | Startups | Initiatives
        </div>
      </div>
      <div className="angelnetworkinvestorheaderAvatar">
        <div className="angelnetworkinvestorheaderPersonName">
          James Bond&nbsp;
        </div>
        <div className="angelnetworkinvestorheaderPersonAvatar">
          <Avatar
            style={{ height: "30px", width: "30px" }}
            src="data:image/jpeg;base64,"
            alt="James Bond"
          />
        </div>
      </div>
    </div>
  );
}

export default Header;
