import React from "react";
import "./ListOfStartupsHeader.css";
import { BiMenuAltLeft } from "react-icons/bi";
// import { CgProfile } from "react-icons/cg";
import sinc from "./sinc.png";
import Avatar from "@material-ui/core/Avatar";
function Header() {
  return (
    <div className="listofstartupsheader">
      <div className="listofstartupsheaderDashboard">
        <div className="listofstartupsheaderIcon">
          <BiMenuAltLeft size={30} />
        </div>
        <div className="listofstartupsheaderDashboard_dashboard">Dashboard</div>
      </div>
      <div className="listofstartupsheaderLogo">
        <div className="listofstartupsheaderCellName">
          <img src={sinc} alt="logo" />
          <span> Student Incubation Cell</span>
        </div>
        <div className="listofstartupsheaderWork">
          Mentors | Startups | Initiatives
        </div>
      </div>
      <div className="listofstartupsheaderAvatar">
        <div className="listofstartupsheaderPersonName">James Bond&nbsp;</div>
        <div className="listofstartupsheaderPersonAvatar">
          <Avatar
            style={{ height: "30px", width: "30px" }}
            src="data:image/jpeg;base64,"
            alt="James Bond"
          />
        </div>
      </div>
    </div>
  );
}

export default Header;
