import React from "react";
import "./Mentors_StartUpsYouFollowHeader.css";
import { BiMenuAltLeft } from "react-icons/bi";
// import { CgProfile } from "react-icons/cg";
import sinc from "./sinc.png";
import Avatar from "@material-ui/core/Avatar";
function Header() {
  return (
    <div className="mentorsstartupsyoufollowheader">
      <div className="mentorsstartupsyoufollowheaderDashboard">
        <div className="mentorsstartupsyoufollowheaderIcon">
          <BiMenuAltLeft size={30} />
        </div>
        <div className="mentorsstartupsyoufollowheaderDashboard_dashboard">
          Dashboard
        </div>
      </div>
      <div className="mentorsstartupsyoufollowheaderLogo">
        <div className="mentorsstartupsyoufollowheaderCellName">
          <img src={sinc} alt="logo" />
          <span> Student Incubation Cell</span>
        </div>
        <div className="mentorsstartupsyoufollowheaderWork">
          Mentors | Startups | Initiatives
        </div>
      </div>
      <div className="mentorsstartupsyoufollowheaderAvatar">
        <div className="mentorsstartupsyoufollowheaderPersonName">
          James Bond&nbsp;
        </div>
        <div className="mentorsstartupsyoufollowheaderPersonAvatar">
          <Avatar
            style={{ height: "30px", width: "30px" }}
            src="data:image/jpeg;base64,"
            alt="James Bond"
          />
        </div>
      </div>
    </div>
  );
}

export default Header;
