import React from "react";
import "./AngelNetworkMentorHeader.css";
import { BiMenuAltLeft } from "react-icons/bi";
import sinc from "./sinc.png";
import Avatar from "@material-ui/core/Avatar";
function Header() {
  return (
    <div className="angelnetworkmentorheader">
      <div className="angelnetworkmentorheaderDashboard">
        <div className="angelnetworkmentorheaderIcon">
          <BiMenuAltLeft size={30} />
        </div>
        <div className="angelnetworkmentorheaderDashboard_dashboard">
          Dashboard
        </div>
      </div>
      <div className="angelnetworkmentorheaderLogo">
        <div className="angelnetworkmentorheaderCellName">
          <img src={sinc} alt="logo" />
          <span> Student Incubation Cell</span>
        </div>
        <div className="angelnetworkmentorheaderWork">
          Mentors | Startups | Initiatives
        </div>
      </div>
      <div className="angelnetworkmentorheaderAvatar">
        <div className="angelnetworkmentorheaderPersonName">
          James Bond&nbsp;
        </div>
        <div className="angelnetworkmentorheaderPersonAvatar">
          <Avatar
            style={{ height: "30px", width: "30px" }}
            src="data:image/jpeg;base64,"
            alt="James Bond"
          />
        </div>
      </div>
    </div>
  );
}

export default Header;
